<template>
  <div>
		<crumb :crumbMsg="crumbMsg"></crumb>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<el-cascader
				    placeholder=""
				    :options="options"
				    v-model="filters.optFilters"
				    filterable
				    change-on-select
				    :disabled="this.loadOn.tableLoading"
				  ></el-cascader>
				</el-form-item>
				<el-form-item>
					<el-input
					  placeholder=""
					  v-model="filters.mixing"
					  clearable>
					</el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="mini" v-on:click="searchBtn" icon="el-icon-search" :loading="loadOn.searchLoad" :disabled="this.loadOn.tableLoading"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="mini" @click="selectReset" icon="el-icon-refresh" :loading="loadOn.resetLoad" :disabled="this.loadOn.tableLoading"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<el-col type="flex" class="row-bg toolbar" justify="space-between">
			<el-col :span="4">
				<el-button-group>
					<el-button type="danger" size="mini" @click="handleDels" :disabled="this.delList.length===0 || this.loadOn.tableLoading"></el-button>
		  		<el-button size="mini" @click="handleAdd" :disabled="this.loadOn.tableLoading"></el-button>
	  		</el-button-group>
  		</el-col>
  		<el-col :span="10">
  			<el-button-group>
				  <el-button type="success" size="mini" icon="el-icon-upload"></el-button>
				  <el-button type="" size="mini" style="color:#409EFF"><i class="el-icon-download el-icon--right"></i></el-button>
				</el-button-group>
			</el-col=>
			<el-col :span="10" style="text-align: right">
				<el-button type="success" round size="mini" @click="refreshPage" :loading="refreshLoad"><i class="el-icon-refresh el-icon--left"></i></el-button>
			</el-col>
		</el-col>

		<el-table :data="msgList" highlight-current-row v-loading="loadOn.tableLoading" @sort-change="sortChange" @selection-change="selectChange">
			<el-table-column type="selection">
			</el-table-column>
			<el-table-column type="index" prop="pipe_id">
			</el-table-column>
			<el-table-column
	      label="">
	      <template slot-scope="scope">
	        <el-popover trigger="hover" placement="top">
	          <p>: {{ scope.row.name }}</p>
	          <p>: {{ scope.row.remark }}</p>
	          <p>: {{ scope.row.creator }}</p>
	          <div slot="reference" class="name-wrapper">
	            <el-tag size="medium">{{ scope.row.name }}</el-tag>
	          </div>
	        </el-popover>
	      </template>
	    </el-table-column>
	    <el-table-column prop="goods_num" label="">
			</el-table-column>
			<el-table-column prop="purchase_price" label="" sortable="custom">
			</el-table-column>
			<el-table-column prop="retail_price" label="" sortable="custom">
			</el-table-column>
			<el-table-column prop="parent" label="">
			</el-table-column>
			<el-table-column prop="made_time" label="" sortable="custom">
			</el-table-column>
			<el-table-column prop="edit_time" label="" sortable="custom">
			</el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<el-button-group>
						<el-button size="small"  @click="handleEdit(scope.row)"></el-button>
						<el-button type="danger" size="small" @click="handleDel(scope.row)"></el-button>
					</el-button-group>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
	    <el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="filters.currentPage"
	      :page-sizes="[10, 25, 50, 100, total]"
	      :page-size="filters.pageSize"
	      layout="total, sizes, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	  </div>

	  <!---->
		<el-dialog title="" :visible.sync="loadOn.addFormVisible" :close-on-click-modal="true">
			<el-form :model="addForm" label-width="80px" :rules="FormRules" ref="addForm">
				<el-form-item label="" prop="goods_num">
					<el-input  v-model="addForm.goods_num" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input  v-model="addForm.name" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>

				<el-form-item label="" prop="purchase_price">
					<el-input  v-model="addForm.purchase_price" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>
				<el-form-item label="" prop="retail_price">
					<el-input  v-model="addForm.retail_price" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>

				<el-form-item label="" prop="pipe">
					<el-cascader
				    placeholder=""
				    :options="options"
				    filterable
				    v-model="addForm.pipe"
				    change-on-select
				  ></el-cascader>
				</el-form-item>
				<el-form-item label="" prop="remark">
					<el-input
					  type="textarea"
					  :rows="2"
					  maxlength="100"
					  v-model="addForm.remark"
					  placeholder="">
					</el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="loadOn.addFormVisible = false" size="small"></el-button>
				<el-button type="primary" @click="addSubmit('addForm')" :loading="loadOn.addLoading" size="small"></el-button>
			</div>
		</el-dialog>

	  <!---->
		<el-dialog title="" :visible.sync="loadOn.editFormVisible" :close-on-click-modal="true">
			<el-form :model="editForm" label-width="80px" :rules="FormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input  v-model="editForm.goods_num" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>

				<el-form-item label="" prop="name">
					<el-input  v-model="editForm.name" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>
				<el-form-item label="" prop="purchase_price">
					<el-input  v-model="editForm.purchase_price" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>
				<el-form-item label="" prop="retail_price">
					<el-input  v-model="editForm.retail_price" auto-complete="off" maxlength="25" minlength="1"></el-input>
				</el-form-item>
				<el-form-item label="" prop="pipe">
					<el-cascader
				    placeholder=""
				    :options="options"
				    filterable
				    v-model="editForm.pipe"
				    change-on-select
				  ></el-cascader>
				</el-form-item>
				<el-form-item label="" prop="remark">
					<el-input
					  type="textarea"
					  :rows="2"
					  maxlength="100"
					  v-model="editForm.remark"
					  placeholder="">
					</el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="loadOn.editFormVisible = false" size="small"></el-button>
				<el-button type="primary" @click="editSubmit('editForm')" :loading="loadOn.editLoading" size="small"></el-button>
			</div>
		</el-dialog>

  </div>
</template>
<script>
	import Crumb from 'components/crumb/crumb'
	import {getData, sendData} from 'api/data'
	import {urls, ERR_OK} from 'api/config'
	import {formatList, msgNotice} from 'common/js/dom'
	import {mul} from 'common/js/arit'

	export default {
	  name: 'group',
	  data () {
	  	let validatePrice = (rule, value, callback) => {
        if (/^\d+(\.\d+)?$/.test(+value) === false) {
          callback(new Error(''))
        } else if (('' + value).split('.').length === 2) {
        	if (('' + value).split('.')[1].length > 2) {
        		callback(new Error(''))
        	} else {
        		callback()
        	}
        } else {
        	callback()
        }
      }
	  	return {
	  		crumbMsg: ['', ''],
	  		filters: {
	  			'optFilters': [],
	  			'pageSize': 10,
	  			'currentPage': 1,
	  			'sortName': '',
	  			'orderType': '',
	  			'mixing': ''
	  		},
	  		loadOn: {
	  			'tableLoading': true,
	  			'addFormVisible': false,
        	'editFormVisible': false,
        	'editLoading': false,
        	'addLoading': false,
        	'searchLoad': false,
        	'resetLoad': false,
        	'refreshLoad': false
	  		},
	  		total: 0,
	  		msgList: [],
	  		delList: [],
	  		options: [],
        FormRules: {
        	goods_num: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' }
					],
					name: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 1, max: 25, message: ' 1  25 ', trigger: 'blur' }
					],
					purchase_price: [
						{ required: true, validator: validatePrice, trigger: 'blur' },
						{ min: 1, max: 25, trigger: 'blur' }
					],
					retail_price: [
						{ required: true, validator: validatePrice, trigger: 'blur' },
						{ min: 1, max: 25, trigger: 'blur' }
					],
					pipe: [
						{ required: true, message: '', trigger: 'change' }
					]
				},
				editForm: {},
				addForm: {
					goods_num: '',
					name: '',
					purchase_price: '',
					retail_price: '',
					pipe: [],
					remark: ''
				}
	  	}
	  },
	  created () {
	  	this._getIndex()
	  	this._getMsgList()
	  },
	  methods: {
	  	_getIndex (tip, url) {
	  		tip = urls.goods.goodsIndex.tips
	  		url = urls.goods.goodsIndex.url
	  		getData(tip, url).then((res) => {
          if (res.code === ERR_OK) {
            this.options = formatList(res.data.options)
          }
        })
	  	},
	  	_getMsgList (tip, url) {
	  		this.loadOn.tableLoading = true
	  		tip = urls.goods.goodsList.tips
	  		url = urls.goods.goodsList.url
	  		const data = Object.assign({}, tip, this.filters)

	  		getData(data, url).then((res) => {
          if (res.code === ERR_OK) {
            this.msgList = res.data.msg.data
            this.total = res.data.msg.total
          }
          this.loadOn.tableLoading = false
          this.loadOn.resetLoad = false
          this.loadOn.searchLoad = false
          this.loadOn.refreshLoad = false
        })
	  	},
	  	searchBtn () {
	  		this.loadOn.searchLoad = true
	  		this._getMsgList()
	  	},
	  	selectReset () {
	  		this.loadOn.resetLoad = true
	  		this.filters.optFilters = []
	  		this.filters.mixing = ''
	  		this._getMsgList()
	  	},
	  	refreshPage () {
	  		this.loadOn.refreshLoad = true
	  		setTimeout(() => {
	  			this.loadOn.refreshLoad = false
	  		}, 2000)
	  		this._getMsgList()
	  		this._getIndex()
	  		this.loadOn.addFormVisible = false
        this.loadOn.editFormVisible = false
        this.loadOn.editLoading = false
        this.loadOn.addLoading = false
        this.loadOn.searchLoad = false
        this.loadOn.resetLoad = false
        this.loadOn.refreshLoa = false
	  	},
	  	sortChange (val) {
	  		if (val.prop !== null && val.order !== null) {
	  			this.filters.sortName = val.prop
	  			this.filters.orderType = val.order
	  		} else {
	  			this.filters.sortName = ''
	  			this.filters.orderType = ''
	  		}
	  		this._getMsgList()
	  	},
	  	handleSizeChange (val) {
        this.filters.pageSize = val
        this._getMsgList()
      },
      handleCurrentChange (val) {
        this.filters.currentPage = val
        this._getMsgList()
      },
      selectChange (val) {
      	this.delList = val
      },
      handleDel (row) {
		  	this.$confirm(`${row.name} `, '', {type: 'warning'}).then(() => {
		  		let tip = urls.goods.goodsDel.tips
		  		let url = urls.goods.goodsDel.url
		  		const data = Object.assign({}, tip, {
		  			'delList': [{'goods_num': row.goods_num, 'id': row.id}]
		  		})
          sendData(data, url).then((res) => {
          	let msg = ''
          	let type = ''
	          if (res.code === ERR_OK && res.data.err_ok === 1) {
	          	msg = ''
	          	type = 'success'
	          	this._getMsgList()
	  					this._getIndex()
	          } else if (res.code === 404) {
	          	type = 'warning'
	          	msg = ': '
	          } else if (res.code === 2) {
	          	msg = ': '
	          	type = 'error'
	          } else {
	          	msg = ''
	          	type = 'error'
	          }
	          msgNotice(msg, type, false, true, this)
	        })
        }).catch(() => {
        	return false
        })
      },
      handleDels () {
      	this.$confirm('', '', {type: 'warning'}).then(() => {
		  		let tip = urls.inst.instDel.tips
		  		let url = urls.inst.instDel.url
		  		const data = Object.assign({}, tip, {
		  			'delList': this.delList
		  		})
          sendData(data, url).then((res) => {
          	let msg = ''
          	let type = ''
	          if (res.code === ERR_OK) {
	          	msg = ''
	          	type = 'success'
	          	this._getMsgList()
	  					this._getIndex()
	          } else if (res.code === 2) {
	          	type = ''
	          	msg = `<span style="color:green">${res.data.err_ok}</span> <span style="color: red">${res.data.err}</span> `
	          	if (res.data.err_ok !== 0) {
	          		this._getMsgList()
	  						this._getIndex()
	          	}
	          } else if (res.code === 404) {
	          	type = 'warning'
	          	msg = ': '
	          } else {
	          	msg = ''
	          	type = 'error'
	          }
	          msgNotice(msg, type, true, true, this)
	        })
        }).catch(() => {
        	return false
        })
			},
			handleAdd () {
				this.loadOn.addFormVisible = true
			},
			addSubmit (formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.loadOn.addLoading = true
						let tip = urls.goods.goodsAdd.tips
						let url = urls.goods.goodsAdd.url
						const data = Object.assign({
							'p_price': mul(this.addForm.purchase_price, 100),
							'r_price': mul(this.addForm.retail_price, 100)
						}, tip, this.addForm)
						sendData(data, url).then((res) => {
		          let msg = ''
	          	let type = ''
		          if (res.code === ERR_OK) {
		          	msg = ''
		          	type = 'success'
		          	this._getMsgList()
		  					this._getIndex()
		          } else if (res.code === 404) {
		          	type = 'warning'
		          	msg = ': '
		          } else if (res.code === 2) {
		          	type = 'warning'
		          	msg = ': '
		          } else {
		          	type = 'error'
		          	msg = ''
		          }
		          msgNotice(msg, type, true, true, this)
		          this.loadOn.addLoading = false
		        }).catch(() => {
		        	this.loadOn.addLoading = false
		        	msgNotice('', 'error', false, false, this)
		        })
					} else {
						msgNotice('<span style="color: red"> * </span>', 'warning', true, true, this)
						return false
					}
				})
			},
			handleEdit (row) {
				this.loadOn.editFormVisible = true
				let len = this.options[0].value.length
				let pipe = []
				for (let i = 0; i < (row.pipe_id.length - len) / 4 + 1; i++) {
					pipe.push(row.pipe_id.slice(0, len + i * 4))
				}
				this.editForm = Object.assign({'pipe': pipe}, row)
			},
			editSubmit (formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						this.loadOn.editLoading = true
						let tip = urls.goods.goodsEdit.tips
						let url = urls.goods.goodsEdit.url
						const data = Object.assign({
							'p_price': mul(this.editForm.purchase_price, 100),
							'r_price': mul(this.editForm.retail_price, 100)
						}, tip, this.editForm)
						sendData(data, url).then((res) => {
							let msg = ''
							let type = ''
							if (res.code === ERR_OK) {
								msg = ''
								type = 'success'
								this._getMsgList()
		  					this._getIndex()
							} else if (res.code === 404) {
		          	type = 'warning'
		          	msg = ': '
		          } else if (res.code === 2) {
		          	type = 'warning'
		          	msg = ''
		          } else {
		          	type = ''
		          	msg = 'error'
							}
							msgNotice(msg, type, false, true, this)
							this.loadOn.editLoading = false
						}).catch(() => {
		        	this.loadOn.editLoading = false
		        	msgNotice('', 'error', false, false, this)
		        })
					} else {
						msgNotice('<span style="color: red"> * </span>', 'warning', true, true, this)
						return false
					}
				})
			}
	  },
	  components: {
	  	Crumb
	  }
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
		
</style>


